@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@700&display=swap);
* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  font-family: Nunito;
  scrollbar-width: none;
  -ms-overflow-style: none;
  font-family: Quicksand;
  box-sizing: border-box;
}
::-webkit-scrollbar {
  display: none;
}
body {
  background-image: url(/SampleSpace/static/media/witch_magic.7e8b97b8.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

header {
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  position: fixed;
  z-index: 10;
  overflow: hidden;
  top: 0;
}

.header-height {
  height: 55px;
}

.center {
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.head-wrapper {
  width: 80%;
  margin: 0 auto;
}

.title-wrapper {
  background-color: rgba(44, 44, 44, 0.5);
  color: white;
  width: 10vw;
}

.nav-link {
  color: #f4f4f9;
  padding: 0 20px;
  vertical-align: middle;
  float: right;
  transition: color 0.1s;
}

.nav-link:hover {
  color: #3895d3;
}

.panel {
  background-color: rgba(0, 0, 0, 0.7);
  width: 80%;
  margin: 0 auto;
  padding-top: 100px;
  box-shadow: 2px 2px 10px 1px rgb(0, 0, 0);
}

.banner {
  text-align: center;
  width: 600px;
  height: 10%;
  margin: 0 auto 8vh auto;
}

.banner img {
  height: 100%;
  width: 100%;
  border: 1px solid white;
}

.anime-wrapper {
  margin: 0 auto;
  width: 60%;
  margin-top: 30px;
  display: flex;
  flex-wrap: wrap;
}

.poster {
  display: block;
  width: 33%;
  height: 300px;
  padding: 15px;
}

.content-wrapper {
  width: 100%;
  height: 100%;
  position: relative;
  border: 2px solid white;
}

.content-wrapper img {
  width: 100%;
  height: 100%;
  z-index: 0;
  transition: -webkit-filter 0.2s;
  transition: filter 0.2s;
  transition: filter 0.2s, -webkit-filter 0.2s;
}

.content-wrapper:hover img {
  -webkit-filter: blur(0) brightness(0.5);
          filter: blur(0) brightness(0.5);
}

.cross-box {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 30px;
  height: 30px;
  left: 86%;
  z-index: 3;
  border-bottom-left-radius: 2px;
  /* background: red; */
  cursor: pointer;
}
.spoiler {
  width: 50%;
  height: 50%;
  z-index: 4;
  /* background-color: black; */
}
.cross {
  color: #fff;
  font-family: Quicksand;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.2s;
  z-index: 4;
}

.spoiler:hover div {
  opacity: 90;
}

.anime-info {
  display: flex;
  align-content: center;
  justify-content: center;
  color: white;
  font-size: small;
  background-color: rgba(0, 0, 0, 0.7);
  text-align: center;
  position: absolute;
  padding-top: 7px;
  top: 80%;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 2;
}

.input {
  text-align: center;
  margin-bottom: 20px;
}
.input Input {
  box-sizing: border-box;
  width: 400px;
  height: 50px;
  padding: 10px;
}

.show-animes {
  text-align: center;
}
.show-animes button {
  width: 150px;
  height: 50px;
}

